.App {
  text-align: center;
  background-color: #282c34;
  height: 3000px;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 700px 200px 500px 1000px 50px 500px;
  grid-template-areas: 
  "title-cont"
  "animation"
  "buffer"
  "description"
  "label"
  "contributors";
  overflow-y: hidden;
}

.title-container {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  grid-area: title-cont;
}

.animate-container {
  grid-area: animation;
  margin: auto;
  height: 100%;
  margin-top: 30%;
  font-size: 60px;
  color: white;
}

.description-container {
  margin: auto;
  grid-area: description;
  height: 100%;
  width: 800px;
  color: white;
  border-radius: 20px;
  padding: 30px;
}

.products-container {
  margin: auto;
  display: flex;
  width: 600px;
  justify-content: space-around;
  padding: 20px;
  height: 100px;
}

.product {
  border: solid #333941;
  background-color: #24282e;
  border-radius: 10px;
  height: 50px;
  width: 100px;
  padding-top: 20px;
  font-weight: 600;
}

.label {
  grid-area: label;
  font-size: 24px;
  color: white;
}

.contributors-container {
  grid-area: contributors;
  display: flex;
  padding-left: 300px;
  padding-right: 300px;
  justify-content: space-around;
}

#dot {
  font-size: 60px;
  font-weight: 400;
}

#dot-braille {
  font-size: 24px;
}

.name {
  color: white;
}
